<template>
  <div>
    <ul class="viproute-box card-list clearfix">
      <li v-for="(item, index) in cardList" :key="index">
        <div class="content">
            <Row class="mb10">
                <Col span="16">
                    <img :src="item.cardImg" alt />
                </Col>
                <Col class="card-num" span="8">尾号：{{item.cardNo}}</Col>
            </Row>
          <p>持卡人姓名：{{item.userName}}</p>
          <p>手机号：{{item.userPhone}}</p>
        </div>
      </li>
      <li class="add" @click="isAdd = true">
        <div class="content">
          <p class="icon">
            <Icon type="ios-add-circle-outline" />
          </p>
          <p class="tip">添加银行卡</p>
        </div>
      </li>
    </ul>
    <Modal
      v-model="isAdd"
      title="新增银行卡"
      :mask-closable="false"
      :footer-hide="true"
    >
      <Form ref="addForm" :model="addForm" :rules="addRule" :label-width="100">
        <FormItem label="真实姓名" prop="userName">
          <Input type="text" :maxlength="25" v-model="addForm.userName" placeholder="请输入持卡人姓名" />
        </FormItem>
        <FormItem label="身份证号" prop="idNum">
          <Input type="text" :maxlength="18" v-model="addForm.idNum" placeholder="请输入身份证号" />
        </FormItem>
        <FormItem label="银行卡号" prop="cardNo">
          <Input v-model="addForm.cardNo" type="text" placeholder="请输入银行卡号" />
        </FormItem>
        <FormItem label="手机号" prop="userPhone">
          <Input v-model="addForm.userPhone" placeholder="请输入该卡银行预留手机号" />
        </FormItem>
        <FormItem label="验证码" prop="smsCode">
          <Row>
            <Col span="16">
              <Input v-model="addForm.smsCode" placeholder="请输入验证码" />
            </Col>
            <Col span="7" offset="1">
              <Button :disabled="!isGetCode" type="primary" @click="getTelCode">{{getCodeTip}}</Button>
            </Col>
          </Row>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="addCard" class="mr8">添加</Button>
          <Button @click="reset">重置</Button>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
import * as validate from "@/utils/verification";
export default {
  data() {
    return {
      cardList: [],
      isAdd: false,
      addForm: {
        userName: '',
        idNum: '',
        cardNo: '',
        userPhone: '',
        smsCode: ''
      },
      addRule: {
          userName: [{ required: true, message: "请输入用户名", trigger: "blur" }
          ],
          idNum:[{ required: true, validator: validate.idCard, trigger: "blur" }],
          cardNo:[{ required: true, message: "请输入银行卡号", trigger: "blur" }],
          userPhone: [{ required: true, validator: validate.phone, trigger: "blur" }],
          smsCode: [{ required: true, message: "请输入验证码", trigger: "blur" }]
      },
      isGetCode: true, //正在获取验证码
      getCodeTip: "获取验证码"
    };
  },
  created() {
    this.getBankCard();
  },
  methods: {
    getBankCard() {
      this.$api
        .bankCard()
        .then(res => {
          this.cardList = res.data.data;
        })
        .catch(err => console.log(14,err));
    },
    // 获取验证码
    getTelCode() {
      if (this.isGetCode && !this.addForm.userPhone) return;
      this.isGetCode = false;
      this.$api
        .getTelCode({
          moblie: this.addForm.userPhone,
          type: "0005"
        })
        .then(res => {
          let timer = 0;
          let seconde = 0;
          let recover = 60 * 5;
          clearInterval(timer);
          timer = setInterval(() => {
              console.log(timer)
            if (seconde == recover) {
              clearInterval(timer);
              this.getCodeTip = "重新获取";
              this.isGetCode = true;
            } else {
              seconde += 1;
              this.getCodeTip = `${recover - seconde}s`;
            }
          }, 1000);
        })
        .catch(err => {
          console.log(err);
          this.isGetCode = true;
        });
    },
    // 重置
    reset() {
      this.$refs.addForm.resetFields();
    },
    // 添加银行卡
    addCard() {
        this.$api.addCard(this.addForm).then(res => {
            this.getBankCard();
        }).catch(err => console.log(err))
    }
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/theme.less";
@import "~@/less/mixins.less";
.card-list {
  li {
    float: left;
    width: 33.33%;
    padding: 5px;
    .content {
      border: 1px solid @borderColor;
      border-radius: 5px;
      overflow: hidden;
      padding: 5px 10px;
      line-height: 24px;
      height: 138px;
      color: #999;
      img {
        display: block;
        height: 36px;
      }
      .card-num{
          line-height: 36px;
          color: #333;
          font-size: 15px;
          font-weight: 700;
      }
    }
  }

  .add {
    text-align: center;
    cursor: pointer;
    .icon {
      font-size: 36px;
      margin: 20px 0;
      color: @borderColor;
    }
    .tip {
      color: @borderColor;
      margin-bottom: 20px;
    }
    &:hover {
      .content {
        border-color: @themeColor;
      }
      .tip,
      .icon {
        color: @themeColor;
      }
    }
  }
}
</style>



